.root {
	min-height: 52px;
	padding-right: 24px;
	padding-left: 24px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	position: relative;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-pack: end;
	    -ms-flex-pack: end;
	        justify-content: flex-end;
	color: var(--mdc-theme-text-primary-on-background);
	font-size: var(--mdc-theme-font-size-small);
	font-family: var(--mdc-theme-font-family);
	border-top: 1px solid var(--mdc-theme-separator);
}

.root .details {
	width: auto;
	margin-right: 16px;
	-webkit-box-pack: end;
	    -ms-flex-pack: end;
	        justify-content: flex-end;
	font-weight: var(--mdc-theme-heading-font-weight);
}

.paginationList {
	padding-left: 0;
}

.pageNav,
.pageNumber {
	background: none;
	border: 0;
	font-size: inherit;
	padding: 0;
}

.pageNav {
	color: var(--mdc-theme-text-secondary-on-background);
	font-size: var(--mdc-theme-font-size-small);
	font-family: var(--mdc-theme-font-family);
	margin: 0 16px;
}

.pageNumber {
	color: var(--mdc-theme-text-secondary-on-background);
	font-size: var(--mdc-theme-font-size-small);
	font-family: var(--mdc-theme-font-family);
	margin: 0 4px;
}

.active {
	color: var(--mdc-theme-text-primary-on-background);
}
