.root {
	display: grid;
	grid-template-columns: [byline-start] 1fr [byline-end content-start] 1fr [col-start] 1fr [col-end content-end aside-start] 1fr [aside-end];
	grid-gap: var(--line2);
	padding-bottom: var(--line3);
	max-width: 3840px; /* 4K */
	align-self: center;
}
.root > div:nth-of-type(1) {
	grid-column: content-start / span 2;
	background-color: var(--bg-muted);
	color: #fff;
}
.root > div:nth-of-type(2) {
	background-color: var(--ap);
	color: #fff;
}
.fullColumn {
	grid-column-end: aside-end;
}
.leftColumn { }
.leftColumn,
.asideColumn {
	line-height: 0;
}
.asideColumn {
	grid-column: aside-start / aside-end;
	padding-bottom: var(--line);
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-webkit-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
}
.asideStart,
.asideColumn.asideStart {
	grid-row-start: 2;
	-webkit-box-pack: start;
	    -ms-flex-pack: start;
	        justify-content: flex-start;
}
.asideColumn figcaption {
	padding-right: var(--line);
}
.autoColumn { }
.autoColumnWide {
	grid-column: content-start / content-end;
}
@media (max-width: 1280px) {
	.autoColumn {
		grid-column: content-start / content-end;
	}
}
@media (max-width: 620px) {
	.leftColumn {
		min-width: 156px;
	}
	.asideColumn {
		grid-column: byline-start / byline-end;
		grid-row-start: 3;
	}
	.autoColumn {
		grid-column: content-start / aside-end;
		padding-right: var(--line2);
	}
}
@media (max-width: 490px) {
	.leftColumn,
	.asideColumn,
	.autoColumn {
		grid-column: byline-start / aside-end;
	}
	.autoColumn:first-of-type {
		grid-row-start: 3;
	}
	.leftColumn figcaption {
		padding-right: var(--line);
	}
	.leftColumn {
		grid-row-start: 4;
	}
	.asideColumn {
		grid-row-start: 5;
	}
	.asideColumn,
	.autoColumn {
		padding-left: 24px;
		padding-right: 24px;
	}
}
@media (max-width: 250px) {
	.asideColumn,
	.autoColumn {
		padding-left: 8px;
		padding-right: 8px;
	}
}
.headline {
	composes: ui from './theme/material/_ui.m.css';
	display: -webkit-box !important;
	display: -ms-flexbox !important;
	display: flex !important;
	-ms-flex-line-pack: baseline;
	    align-content: baseline;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	padding-left: var(--line);
	padding-right: var(--line);
}
.headline > * {
	margin-bottom: var(--pt) !important;
}
.orange { --bg: #ff7a00; --bg-muted: #e03c05; }
.amber { --bg: #ffaf00; --bg-muted: #f36f0b; }
.blue { --bg: #0d7ecc; --bg-muted: #0059a2; }
.purple { --bg: #c30c70; --bg-muted: #96004b; }
.pink { --bg: #eb2f59; --bg-muted: #bb0036; }
.green { --bg: #dfdc00; --bg-muted: #b1b200; }
.cyan { --bg: #00b1cc; --bg-muted: #0089a0; }
.red { --bg: #dc0005; --bg-muted: #aa0000; }
