.root {
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
}

.linksWrapper {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-flex: 1;
	    -ms-flex-positive: 1;
	        flex-grow: 1;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	overflow: hidden;
}

.prev,
.next {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	justify-items: center;
}

.icon {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	justify-items: center;
}

.prev .label,
.next .label {
	display: none;
}

.link,
.currentPage {
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	background-color: unset;
	color: var(--mdc-theme-on-surface);
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	font-size: var(--mdc-font-size-small);
	height: calc(var(--mdc-grid-base) * 4);
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	margin: 0 calc(var(--mdc-grid-base) * 0.375);
	min-width: calc(var(--mdc-grid-base) * 4);
	padding: 0;
}

.link:hover,
.currentPage {
	background-color: var(--mdc-theme-primary);
	border-radius: 50%;
	color: var(--mdc-theme-on-primary);
}

.link {
	cursor: pointer;
	border: 0;
}
