.root {
	font-family: var(--mdc-theme-font-family);
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	-webkit-box-shadow: var(--mdc-theme-elevation-1);
	        box-shadow: var(--mdc-theme-elevation-1);
}

.titleButton {
	composes: nativeResetRoot from './button.m.css';

	background-color: var(--mdc-theme-surface);
	cursor: pointer;
	position: relative;
	padding: 12px 24px;
	width: 100%;
	margin: 0;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: reverse;
	    -ms-flex-direction: row-reverse;
	        flex-direction: row-reverse;
	-webkit-transition: padding ease-in-out 0.25s, background-color ease-in-out 0.25s;
	transition: padding ease-in-out 0.25s, background-color ease-in-out 0.25s;
}

.titleButton:active {
	background-color: var(--mdc-theme-background-selected);
	outline: none;
}

.titleButton:focus {
	outline: none;
}

.arrow {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
}

.open .titleButton {
	padding: 24px;
}

.content {
	padding: 24px;
	padding-top: 0;
	-webkit-transition: margin-top ease-in-out 0.25s;
	transition: margin-top ease-in-out 0.25s;
}
